.header {
  position: relative;
  background-color: #008aff;
  height: 752px;
  padding: 48px 20px 0;
  border-bottom-left-radius: 200px;

  &__logo {
    width: 60px;
    height: 28px;
    margin-right: 64px;
  }

  &__nav {
    max-width: 1090px;
    margin: 0 auto 42px;
  }

  &__container {
    display: flex;
    max-width: 1090px;
    margin: 0 auto;
    font-family: "Poppins", sans-serif;
  }

  &__text-block {
    margin-top: 120px;
  }

  &__heading {
    width: 490px;
    height: 136px;
    margin-bottom: 32px;
    font-weight: 600;
    font-size: 64px;
    line-height: 68px;
    letter-spacing: -2px;
    color: #fff;
  }

  &__text {
    width: 460px;
    height: 81px;
    margin-bottom: 56px;
    font-family: "Open sans", sans-serif;
    font-size: 18px;
    line-height: 150%;
    color: #fff;
  }

  &__button {
    width: 226px;
    height: 50px;
    font-weight: 600;
    font-size: 15px;
    line-height: 22px;
    color: #fff;
    background: #ff9c8e;
    border-radius: 64px;
    cursor: pointer;
    border-style: none;
    @include hover(background-color, #fff);
    @include hover(color, #FF9C8E);
  }

  &__slider {
    position: absolute;
    width: 43%;
    right: 0;
    min-width: 500px;
    height: 680px;
    border-top-left-radius: 200px;
    background-image: url(/images/slider/slider-img-new.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

  }
}

.slider {
  display: flex;
  align-items: center;

  &__button {
    width: 32px;
    height: 32px;
    margin-top: 570px;
    border-radius: 50%;
    border-style: none;
    cursor: pointer;
    outline: none;

    &:hover {
      border: 1px solid #9fadc2;
    }

    &:active {
      background-color: #9fadc2;
    }
  }

  &__button-left {
    margin-left: 48px;
    background-image: url(/images/slider/arrow_left.png);
    background-position: center;
    background-repeat: no-repeat;
  }

  &__button-right {
    margin-left: 8px;
    background-image: url(/images/slider/arrow_right.png);
    background-position: center;
    background-repeat: no-repeat;
  }

  &__text-block {
    margin-left: 24px;
    margin-top: 564px;
  }

  &__heading {
    font-size: 13px;
    line-height: 19px;
    font-weight: 600;
    letter-spacing: 3px;
    margin-bottom: 8px;
    text-transform: uppercase;
    color: #fff;
  }

  &__text {
    font-family: "Open sans", sans-serif;
    font-size: 14px;
    line-height: 20px;
    width: 264px;
    color: #fff;
  }
}
